import eventFilterComponent from "@/components/eventFilterComponent";
import bannerImg from '../../assets/events/banner.png';
import { EVENT_APPLICATIONS, EVENT_LOCATION_LIST, EVENTS_CATEGORY, EVENTS_LIST, TAGS_LIST } from "@/api/api";
import eventDetailCard from "@/components/eventDetailCard";
import bookEventForm from "@/components/bookEventForm";
import BookEventList from "@/components/bookEventList";
import { updateWindowHeight } from "@/utils/tools";
export default {
  name: "list",
  component: {
    eventFilterComponent,
    eventDetailCard,
    bookEventForm,
    BookEventList
  },

  data() {
    return {
      bannerImg,
      eventPage: 1,
      eventLimit: 6,
      eventTotalNum: 0,
      showLoadingStatus: false,
      eventsList: [],
      showVendorCompanyData: [],
      categoryOptions: [],
      categoryId: undefined,
      locationValue: '',
      locationOptions: [],
      tagValue: [],
      tagsData: [],
      filterIsOnlineValue: false,
      eventDialogVisible: false,
      eventDetailData: {},
      filterResultData: {},
      bookEventDialogVisible: false,
      bookListDialogVisible: false,
      bookedListData: []
    };
  },

  unmounted() {
    updateWindowHeight();
    window.onresize = null;
  },

  mounted() {
    let screenWidth = document.body.clientWidth;
    let screenWidthFloor = Math.floor(screenWidth);

    if (screenWidthFloor <= 768) {
      updateWindowHeight();
    }

    window.onresize = () => {
      if (screenWidthFloor <= 768) {
        updateWindowHeight();
      }
    };

    this.getEventLocationList();
    this.getEventCategories();
    this.getEventTags();
    this.getEventsList(this.eventPage, this.eventLimit);
  },

  methods: {
    getEventLocationList() {
      let params = {};
      EVENT_LOCATION_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.locationOptions = res.message;
        }
      }).catch(err => {
        console.log(err);
      });
    },

    showBookEvent(item) {
      if (item.online_url) {
        window.open(item.online_url, '_blank');
      } else {
        this.eventDetailData = item;
        this.bookEventDialogVisible = true; // this.eventDialogVisible = false;
      }
    },

    showBookList(item) {
      this.bookListDialogVisible = true;
      let params = {
        event_id: item.id
      };
      EVENT_APPLICATIONS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.bookedListData = res.message.data;
        }
      }).catch(err => {
        console.log(err);
      });
    },

    confirmFilterSearch(e) {
      console.log(e);
      this.filterResultData = e;
      this.eventPage = 1;
      this.getEventsList(this.eventPage, this.eventLimit);
    },

    showEventDialog(item) {
      // console.log(item)
      this.eventDetailData = item;
      this.eventDialogVisible = true; // console.log(item)
      // let params = {
      //   event_id: item.id
      // }
      // EVENTS_DETAIL(params).then(res => {
      //   console.log(res)
      //   if (res.code == 200) {
      //     this.eventData = res.message;
      //   }
      // }).catch(err => {
      //   console.log(err)
      // })
    },

    selectIsOnline() {
      this.filterIsOnlineValue = !this.filterIsOnlineValue;
      this.eventsList = [];
      this.showLoadingStatus = true;
      this.getEventsList(this.dealPage, this.dealLimit);
    },

    locationChange(e) {
      // console.log(e)
      this.eventsList = [];
      this.showLoadingStatus = true;
      this.locationValue = e;
      this.getEventsList(this.dealPage, this.dealLimit);
    },

    tagChange(e) {
      // console.log(e)
      this.eventsList = [];
      this.showLoadingStatus = true;
      this.tagValue = e;
      this.getEventsList(this.dealPage, this.dealLimit);
    },

    getEventTags() {
      let params = {
        type: 2,
        page: 1,
        limit: 10000
      };
      TAGS_LIST(params).then(res => {
        if (res.code == 200) {
          this.tagsData = res.message.data;
        }
      }).catch(err => {
        console.log(err);
      });
    },

    getEventCategories() {
      let params = {
        page: 1,
        limit: 10000
      };
      EVENTS_CATEGORY(params).then(res => {
        this.categoryOptions = res.message.data;
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    closeVendorCompany(i) {
      let index = this.showVendorCompanyData.indexOf(i);

      if (index == -1) {
        this.showVendorCompanyData.push(i);
      } else {
        this.showVendorCompanyData.splice(index, 1);
      }
    },

    eventPageSizeChange(e) {
      console.log(e);
    },

    eventPageChange(e) {
      this.showLoadingStatus = true;
      this.eventPage = e;
      this.$refs.eventScroll.scrollTo(0, 0);
      this.getEventsList(e, this.eventLimit);
    },

    getEventsList(page, limit) {
      let filterResult = this.filterResultData;
      let paramsA = {
        page: page,
        limit: limit
      };
      let params = Object.assign(paramsA, filterResult);
      EVENTS_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.eventsList = res.message.data;
          this.eventTotalNum = res.message.total;
          this.showLoadingStatus = false;
        }
      }).catch(err => {
        console.log(err);
      });
    },

    turnDetail(id, t) {
      this.$router.push({
        path: '/events/detail',
        query: {
          id: id,
          t: t
        }
      });
    },

    postEventWhenEmpty() {
      let token = localStorage.getItem('token');

      if (token) {
        this.$router.push({
          path: '/events/post'
        });
      } else {
        this.$router.push({
          path: '/post-event',
          query: {}
        });
      }
    }

  }
};